<script>
export default {
	name: 'zov-tab-pane',
	props: {
		// tab-pane
		id: {
			type: String,
			required: true
		},
		// tab
		label: String,
		// tab
		order: {
			type: Number,
			default: 0
		},
		// tab
		disabled: {
			type: Boolean,
			default: false
		},
		// 
		lazy: {
			type: Boolean,
			default: true
		},
		closable: {
			type: Boolean,
			default: false
		}
	},
	created() {
		if (this.Tabs === null) {
			throw new Error('TabPaneTabs')
		}
		this.Tabs.addTab(this)
	},
	destroyed() {
		this.Tabs.removeTab(this.id)
	},
	inject: {
		Tabs: {
			default: null
		}
	},
	data() {
		return {
			computedLabel: ''
		}
	},
	render() {
		this.computedLabel = this.$slots.label || this.label
		return <span class="zov-tab-pane" />
	}
}
</script>
